.tabs {
}

// Abas
.tabs-abas {
  // Aba (Item)
  li {
    width: auto;
    display: block;
    float: left;
    text-align: center;
    cursor: pointer;
    padding: 15px;
    transition: 0.3s ease-out;
    color: $cor_fonte_escura;
    box-shadow: inset 0px -3px 0px 0px #ddd;

    &:hover {
      background: rgba(0, 0, 0, 0.05);
    }

    @include mq(s) {
      width: 100% !important;
    }

    &.active {
      box-shadow: inset 0px -3px 0px 0px $cor_primaria;
      background: rgba(0, 0, 0, 0.05);
      cursor: default;
    }

    span {
      vertical-align: top;
    }

    i {
      font-size: 24px;
      margin-right: 10px;
    }
  }
}

// Conte√∫do
.tab-content {
  display: none;

  // Exibe
  &.active {
    display: block;
  }
}
