// Lista v√≠deos
.lista-videos {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

// Bloco v√≠deo
.bloco-video {
  position: relative;
  margin-bottom: 20px;
}
.bloco-video-wrapper {
  position: relative;
  display: block;
  width: 100%;
  padding: 0;
  border-radius: 8px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  overflow: hidden;

  &::before {
    display: block;
    content: "";
    padding-top: calc(9 / 16 * 100%);
  }

  iframe {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
    border: 0;
  }
}
.bloco-video-infos {
  margin-top: 5px;
}
.bloco-video-titulo {
  font-weight: 600;
  color: $cor_fonte_escura;
  text-align: center;
}

// Thumb com play
.bloco-video-thumb {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.bloco-video-thumb-play {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 65px;
  height: 65px;
  border-radius: 50%;
  background-color: rgba(#fff, 0.5);
  color: $cor_primaria;
  font-size: 1.5em;
  cursor: pointer;
  transition: 0.3s;

  &:hover {
    background-color: $cor_primaria;
    color: #fff;
    transform: scale(1.1);
  }

  i {
    margin-left: 0.25em;
  }
}
