// Box-sizing
*,
*::after,
*::before {
  box-sizing: border-box;
}

// Remove outline de foco do firefox e outros browsers
:focus {
  outline: none;
}
::-moz-focus-inner {
  border: 0;
}

// Cor do texto selecionado
::selection,
::-moz-selection {
  background: rgba($cor_primaria, 0.5);
}

// Remove o fundo azul de itens clicados no chrome para android
* {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

// Body
body {
  background: #000319;
  font-family: $fonte_geral;
  font-size: 16px;
  line-height: 1.4;
  color: $cor_fonte_clara;
}

// Fix imagens
img {
  display: block;
  max-width: 100%;
  height: auto;
}

// Se√ß√µes
.secao {
  position: relative;
  padding-top: 40px;
  padding-bottom: 40px;

  // MOBILE
  @include mq(s) {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  &.grey {
    background: $cor_fundo2;
  }
}
// Previne o scroll lateral
.overflow-hidden {
  overflow: hidden;
}

// Oculta o elemento no DESKTOP
.hide-desktop {
  @include mq(l) {
    display: none !important;
  }
}
// Oculta o elemento no TABLET
.hide-tablet {
  @include mq(m) {
    display: none !important;
  }
}
// Oculta o elemento no MOBILE
.hide-mobile {
  @include mq(s) {
    display: none !important;
  }
}

// Fundo com imagem
.bg-image {
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

// Fundo com blur
.bg-blur {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  filter: blur(8px);
}

// M√°scara
.mascara {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: rgba(darken($cor_primaria, 10%), 0.9);
}

// Bot√£o CTA (Home)
.btn-cta-home {
  // MOBILE
  @include mq(s) {
    margin-top: 10px;
  }

  .btn {
    // MOBILE
    @include mq(s) {
      padding: 14px 22px;
      font-size: 14px;
    }
  }
}

// Margens
.mt-0 {
  margin-top: 0 !important;
}
.mt-1 {
  margin-top: 10px !important;
}
.mt-2 {
  margin-top: 20px !important;
}
.mt-3 {
  margin-top: 30px !important;
}
.mt-4 {
  margin-top: 40px !important;
}
.mb-0 {
  margin-bottom: 0 !important;
}
.mb-1 {
  margin-bottom: 10px !important;
}
.mb-2 {
  margin-bottom: 20px !important;
}
.mb-3 {
  margin-bottom: 30px !important;
}
.mb-4 {
  margin-bottom: 40px !important;
}

// Padding
.pt-0 {
  padding-top: 0 !important;
}
.pt-1 {
  padding-top: 10px !important;
}
.pt-2 {
  padding-top: 20px !important;
}
.pb-0 {
  padding-bottom: 0 !important;
}
.pb-1 {
  padding-bottom: 10px !important;
}
.pb-2 {
  padding-bottom: 20px !important;
}

// Alinha ao centro com flex
.flex-center {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

// Breadcrumb
.breadcrumb {
  padding: 10px 20px;
  border-radius: 4px;
  background-color: $cor_fundo2;
}
.breadcrumb li {
  display: inline-block;
}
.breadcrumb li + li::before {
  content: "/";
  display: inline-block;
  margin: 0 8px;
  color: rgba(#000, 0.3);
}
.breadcrumb a {
  color: $cor_primaria;
  transition: 0.3s ease-in-out;

  &:hover,
  &:focus {
    color: darken($cor_primaria, 10%);
  }
}

// Shimmer Effect (Lazyload)
.shimmer:not(.lazyloaded) {
  background: linear-gradient(to right, #eff1f3 4%, #e2e2e2 25%, #eff1f3 36%);
  background-size: 1000px 100%;
  border-radius: 8px;
  animation: shimmer-bg 2s infinite linear;

  &.shimmer-circle {
    border-radius: 50%;
  }
}
@keyframes shimmer-bg {
  0% {
    background-position: -1000px 0;
  }
  100% {
    background-position: 1000px 0;
  }
}
