// Se√ß√£o
.apresentacao {
  padding-top: 140px;
  padding-bottom: 120px;
  color: #fff;

  // DESKTOP
  @include mq(l) {
    display: flex;
    align-items: center;
    min-height: 100vh;
  }

  // MOBILE
  @include mq(s) {
    padding-top: 100px;
    padding-bottom: 60px;
  }
}

// M√°scara fundo
.apresentacao .mascara {
  background: linear-gradient(to right, rgba(#000, 0.9), rgba(#000, 0.6));

  // MOBILE
  @include mq(s) {
    background: linear-gradient(to bottom, rgba(#000, 0.9), rgba(#000, 0.6));
  }
}

.apresentacao.bg-image {
    background-attachment: scroll;
    background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;
}

// Conte√∫do se√ß√£o (flex)
.aps-content {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

// Informa√ß√µes
.aps-infos {
  text-align: left;
  padding-right: 30px;

  &.center {
    text-align: center;
    padding-right: 0px;
  }

  // MOBILE
  @include mq(s) {
    text-align: center;
    padding-right: 0px;
  }
}

// T√≠tulo
.aps-titulo {
  float: none;
  font-size: 36px;
  font-weight: bold;
  color: #fff;

  // MOBILE
  @include mq(s) {
    font-size: 22px;
  }

  b,
  strong {
    color: $cor_primaria;
  }
}

// Subt√≠tulo
.aps-subtitulo {
  float: none;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #fff;

  // MOBILE
  @include mq(s) {
    font-size: 18px;
  }

  b,
  strong {
    color: $cor_primaria;
  }
}

// Texto
.aps-texto {
  font-size: 18px;
  font-weight: 300;
  color: #fff;

  // MOBILE
  @include mq(s) {
    font-size: 16px;
  }

  b,
  strong {
    font-weight: 600;
  }
}

// Lista
.aps-texto ul {
  list-style: none !important;
  padding-left: 0px;

  // MOBILE
  @include mq(s) {
    padding-left: 20px;
  }

  li {
    position: relative;
    text-align: left;

    // Check
    &::before {
      content: "";
      display: inline-block;
      vertical-align: baseline;
      margin-right: 10px;
      width: 8px;
      height: 15px;
      border-bottom: 2px solid $cor_primaria;
      border-right: 2px solid $cor_primaria;
      transform: rotate(45deg);
    }
  }
}

// Bot√£o
.aps-btn {
  margin-top: 30px;
  text-align: left;

  // MOBILE
  @include mq(s) {
    margin-top: 20px;
    text-align: center;
  }
}
.aps-infos.center .aps-btn {
  text-align: center;
}

// Segura formul√°rio
.aps-form {
}
// Card formul√°rio
.aps-form-card {
  position: relative;
  z-index: 2;
  border-radius: 12px;
  padding: 30px;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  text-align: center;
  color: $cor_fonte_escura;
  overflow: hidden;

  // MOBILE
  @include mq(s) {
    margin-bottom: 20px;
  }
}
.aps-form-titulo {
  font-size: 24px;
  line-height: 1.2;
  font-weight: bold;
  color: $cor_primaria;
  margin-bottom: 20px;

  // MOBILE
  @include mq(s) {
    font-size: 20px;
  }
}
.aps-form-subtitulo {
  margin-top: -7px;
  margin-bottom: 25px;
}
.aps-form-btn {
  margin-top: 20px;
}
.aps-form-small {
  margin-top: 12px;
  font-size: 12px;
  line-height: 1.2;
}
// Campos
.aps-form-card .campo-container {
  .campo:not(:focus) {
    border-color: #ccc;
  }

  &.campo-icon label.icon i {
    color: $cor_primaria;
  }
}

// Imagem
.aps-image-wrp {
}
.aps-image {
  border-radius: 12px;
  overflow: hidden;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;

  // MOBILE
  @include mq(s) {
    margin-bottom: 20px;
  }

  img {
    transition: 0.5s ease-in-out;
  }
  &:hover img {
    transform: scale(1.2);
  }
}

// V√≠deo
.aps-video-wrp {
}
.aps-video-center {
  display: flex;
  justify-content: center;
}
.aps-video {
  .bloco-video-wrapper {
    border-radius: 12px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  }
}
